.about-page {
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 15px;
    background-image: url('../../img/home.jpg');
    background-position: center;
    background-size: cover;
    /* color: #F2DEAF; */
    color: #fff;
    /* margin-top: -60px; */
    /* overflow: hidden;    */
    
}

.about-box {
    background-color: rgba(216, 213, 213, 0.5);
    padding: 30px;
    backdrop-filter: blur(15px) saturate(180%);
    border-radius: 25px;
    max-width: 600px;
    max-height: 80%;
}

.about-header {
    text-align: center;
    padding: 10px;
    color: #181818;
}

.about-box p {
    text-align: center;
}